.nav {
  background: #1b1f23;
  color: white;
  border-bottom: 1px solid #2b2f33;
}
a img {
  width: 187px;
  height: 48px;
}

.mg-bt_20 {
  margin-bottom: 20px;
}

.hd {
  overflow: hidden;
}

.navbar {
  display: inline-block;
  position: relative;
  text-decoration: none;
}

/* Dropdown p */
.drop {
  color: white;
  padding: 16px 16px 25px 16px;
  font-size: 16px;
  font-weight: 600;
  border: none;
}

.drop:hover {
  cursor: default;
}

.drop i {
  margin-left: 5px;
  margin-bottom: 5px;
}

.color-gr:hover {
  color: rgb(5, 148, 5);
  cursor: pointer;
}

.arrow {
  border: solid rgb(255, 255, 255);
  border-width: 0 1.5px 1.5px 0;
  display: inline-block;
  padding: 3px;
}
.down {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

/* The container <div> - needed to position the dropdown content */
.dropdown {
  position: relative;
}

/* Dropdown Content (Hidden by Default) */
.dropdown-content {
  display: none;
  padding-top: 20px;
  padding-bottom: 20px;
  position: absolute;
  background-color: #191e1e;
  min-width: 220px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  z-index: 1;
}

/* Links inside the dropdown */
.dropdown-content a {
  display: block;
  color: white;
  padding: 7px 25px;
  text-decoration: none;
}

.dropdown-content div {
  display: block;
  padding: 17px 25px 12px 25px;
  font-weight: 700;
  color: white;
}

/* Change color of dropdown links on hover */
.dropdown-content a:hover {
  color: rgb(5, 148, 5);
  transition: color 0.6s;
}

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {
  transition: all 2s;
  display: block;
}

.dropdown:hover .dropbtn {
  background-color: #3e8e41;
}

.outer-link svg {
  width: 14px;
  height: 18px;
  margin-left: 2px;
  padding-top: 6px;
}

.burger-menu {
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  display: none;
  position: absolute;
  right: 0;
  top: 0;
  padding-top: 40px;
  padding-right: 20px;
}

.burger-line {
  width: 25px;
  height: 3px;
  color: white;
  background: white;
  transition: all 0.5s ease-in-out;
}
.burger-line::before,
.burger-line::after {
  content: "";
  position: absolute;
  width: 25px;
  height: 3px;
  background: white;
  transition: all 0.5s ease-in-out;
}
.burger-line::before {
  transform: translateY(-8px);
}
.burger-line::after {
  transform: translateY(8px);
}

/* ANIMATION */
.burger-menu.open .burger-line {
  transform: translateX(-50px);
  background: transparent;
  box-shadow: none;
}
.burger-menu.open .burger-line::before {
  transform: rotate(45deg) translate(35px, -35px);
}
.burger-menu.open .burger-line::after {
  transform: rotate(-45deg) translate(35px, 35px);
}

.dp-hide {
  display: none;
}

@media screen and (max-width: 900px) {
  .burger-menu {
    display: block;
  }

  .dp-hide {
    display: block;
  }

  .navbar {
    display: none;
  }

  .navbar-mobile {
    display: block;
    flex-direction: column;
  }
}
